<template>
  <b-container :fluid="true" class="main">
    <b-row>
      <b-col v-for="brand in brands" :key="brand.name" lg="2" md="3" sm="4" cols="6" class="">
        <brand-box :brand="brand"></brand-box>
      </b-col>
    </b-row>
    <b-button v-if="!brandLoadingDone" @click="loadBrands">
      Load More
    </b-button>
  </b-container>
</template>

<script>
import BrandBox from '../../components/Boxes/BrandBox'
import loader from '../../mixins/loader'

export default {
  name: 'AllBrands',
  data () {
    return {
      brands: [],
      brandLoadingDone: false
    }
  },
  components: {
    BrandBox
  },
  mixins: [
    loader
  ],
  methods: {
    loadBrands () {
      const offset = this.brands.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/brands/all', { offset, limit }, 'brands')
        .then(response => {
          this.brands = this.brands.concat(response.data.brands)
          if (response.data.brands.length < limit) {
            this.brandLoadingDone = true
          }
        })
    }
  },
  created () {
    this.loadBrands()
  }
}
</script>

<style scoped>
.main {
  background-color: rgb(221, 221, 221);
}
</style>
