<template>
  <b-container :fluid="true" class="main">
    <!-- <b-card class="mt-3" header="Brand">
      <pre class="m-0">{{brand}}</pre>
    </b-card> -->
    <b-carousel
      id="carousel-1"
      :interval="4000"
      controls
      indicators
      img-width="200"
      img-height="200"
      background="gray"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
      class="mb-4"
    >
      <b-carousel-slide>
        <!-- v-for="(image,index) in brand.image" :key="index"> -->
        <template v-slot:img>
          <img width="200"
            height="300"
            background-color="red"
            :src=imgSrc
            class="d-block w-100"
          >
        </template>
      </b-carousel-slide>
    </b-carousel>
    <b-row>
      <b-col v-for="product in products" :key="product.id" class="" cols="6" sm="4" md="3" lg="2">
        <product-box :product="product"></product-box>
      </b-col>
    </b-row>
    <b-container fluid class="d-flex justify-content-center">
      <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
        <b-button :disabled="busy" v-if="!productLoadingDone" variant="success" @click="loadProducts(brand.name)">
          Load More Products
        </b-button>
      </b-overlay>
    </b-container>
  </b-container>
</template>

<script>
import ProductBox from '../../components/Boxes/ProductBox'
import loader from '../../mixins/loader'

export default {
  name: 'CertainBrand',
  data () {
    return {
      brand: {},
      products: [],
      productLoadingDone: true,
      sliding: null,
      busy: false
    }
  },
  computed: {
    imgSrc () {
      return this.brand.image ? this.brand.image.link : ''
    }
  },
  components: {
    ProductBox
  },
  mixins: [
    loader
  ],
  methods: {
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    loadBrand (brandName) {
      this.loader('/brand', { name: brandName }, `brand ${brandName}`)
        .then(response => {
          this.brand = response.data
          console.log('done' + response.data)
        })
    },
    loadProducts (brandName) {
      if (!this.productLoadingDone) this.busy = true
      const offset = this.products.length
      const limit = process.env.VUE_APP_PAGE_LIMIT
      this.loader('/products/brand',
        { name: brandName, offset, limit },
        `products of ${brandName}`)
        .then(response => {
          this.busy = false
          this.products = this.products.concat(response.data.products)
          if (response.data.products.length < limit) {
            this.productLoadingDone = true
          } else {
            this.productLoadingDone = false
          }
        })
    },
    clearProducts () {
      this.products = []
      this.productLoadingDone = true
    },
    loadAll (brandName) {
      this.loadBrand(brandName)
      this.clearProducts()
      this.loadProducts(brandName)
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadAll(to.query.name)
    }
  },
  created () {
    this.loadAll(this.$route.query.name)
  }
}
</script>

<style scoped>
.main {
  background-color: rgb(221, 221, 221);
}
</style>
