<template>
  <b-overlay :show="busy" opacity="0.4" spinner-small
                      spinner-variant="primary" class="d-inline block" @hidden="onHidden">
    <router-link
      id="holder"
      tag="div"
      :to="{ name: 'CertainBrand', query: { name: brand.name } }"
      @click="show=true"
    >
      <b-card
        :title="brand.name"
        :img-src="imgSrc"
        :img-alt="brand.name"
        img-top
      >
        <b-card-text>
          <div id="discount">{{brand.discount}}%</div>
        </b-card-text>
      </b-card>
    </router-link>
  </b-overlay>
</template>

<script>
export default {
  name: 'BrandBox',
  data () {
    return {
      busy: false
    }
  },
  props: [
    'brand'
  ],
  computed: {
    imgSrc () {
      return this.brand.image ? this.brand.image.link : ''
    }
  },
  methods: {
    onHidden () {
      this.$refs.button.focus()
    }
  }
}
</script>

<style scoped>
#holder {
  cursor: pointer;
}
</style>
